import {Component, NgZone, OnInit} from '@angular/core';
import {ApiServiceService} from '../../services/api-service.service';
import {Exchange, SettingModule, Subscribe} from '../../interface/ec-interface';
import {MatTableDataSource} from '@angular/material/table';

export interface TableElement {
  pair: string;
  buy_name: string;
  buy: number;
  sell_name: string;
  sell: number;
  percent: number;
  tokenid: string;
  buyurl: string;
  sellurl: string;
  buy_ask: number;
  sell_bid: number;
  sell_symbol: string;
}


@Component({
  selector: 'app-main-table',
  templateUrl: './main-table.component.html',
  styleUrls: ['./main-table.component.css']
})
export class MainTableComponent implements OnInit {

  constructor(private api: ApiServiceService, private ngZone: NgZone) {
  }

  color = {
    grey: '#bfbfbf', // <2.5
    green: '#a8dab5', // >2.5<5
    red: '#f44336' // 5<
  };
  ExchangeData: Exchange[] = [];
  TableData: TableElement[] = [];
  tempTableData: TableElement[] = [];
  displayedColumns: string[] = ['pair', 'buy', 'sell', 'percent'];
  dataSource = new MatTableDataSource(this.TableData);
  greenKoef = 2.5;
  redKoef = 5;
  pushKoef = 1;
  timer = 2000;
  apiKeys = [];
  tokenPrices = [];
  notices = [];
  noticesTimer = 10;
  freezePercent = 1;
  gasNormal;
  gasFast;
  settingsModules: SettingModule = {
    bancor: true,
    idex: true,
    kyber: true,
    uniswap_v2: true,
    uniswap_v1: true,
  };
  hideDirection = [];

  ngOnInit(): void {
    this.init();
  }

  init = () => {
    this.api.getApi('settings').subscribe((result: Subscribe) => {
      this.apiKeys = result[0].api_keys.keys;
      this.timer = result[0].timeout_refresh_data;
      this.noticesTimer = result[0].timeout_notice;
      this.redKoef = result[0].koef_top;
      this.greenKoef = result[0].koef_low;
      this.pushKoef = result[0].koef_push;
      this.freezePercent = result[0].freeze_percent;
      this.gasNormal = result[0].gas_normal;
      this.gasFast = result[0].gas_fast;
    });
    this.api.getApi('settings_modules').subscribe((result: any[]) => {
      result.forEach(r => {
        switch (r.module_name) {
          case 'bancor':
            this.settingsModules.bancor = r.is_active;
            break;
          case 'idex':
            this.settingsModules.idex = r.is_active;
            break;
          case 'kyber':
            this.settingsModules.kyber = r.is_active;
            break;
          case 'uniswap_v2':
            this.settingsModules.uniswap_v2 = r.is_active;
            break;
          case 'uniswap_v1':
            this.settingsModules.uniswap_v1 = r.is_active;
            break;
        }
      });
    });
    this.getHotbitProfits();
    // this.getPair();
    // this.ngZone.runOutsideAngular(() => {
    // setInterval(() => {
    //   this.getPair();
    // }, this.timer);
    // });
  }

  private getPair = () => {
    this.api.getApi('exchpair/').subscribe((result: Exchange[]) => {
      this.tokenPrices = result;
      setTimeout(() => {
        this.getPair();
      }, this.timer);
    }, error => {
      console.log(error);
      this.getPair();
    });
  }

  private getHotbitProfits = () => {
    this.api.getApi('settings').subscribe((result: Subscribe) => {
      this.apiKeys = result[0].api_keys.keys;
      this.timer = result[0].timeout_refresh_data;
      this.noticesTimer = result[0].timeout_notice;
      this.redKoef = result[0].koef_top;
      this.greenKoef = result[0].koef_low;
      this.pushKoef = result[0].koef_push;
      this.freezePercent = result[0].freeze_percent;
      this.gasNormal = result[0].gas_normal;
      this.gasFast = result[0].gas_fast;
    });
    this.api.getApi('exchpair/').subscribe((result: Exchange[]) => {
      this.tokenPrices = result;
    });
    this.api.getApi('profit_exchanges/').subscribe(res => {
      this.TableData = [];
      res.forEach(item => {
        let isHide = false;
        this.hideDirection.forEach(hide => {
          if (hide.pair === item.pair && hide.buy === item.buy_name && hide.sell === item.sell_name) {
            isHide = !Number(((item.sell - item.buy) / (item.buy / 100)).toFixed(2)) <= hide.percent + this.freezePercent;
          }
        });
        if (!isHide) {
          const elem: TableElement = {
            pair: item.pair,
            buy_name: item.buy_name,
            buy: item.buy,
            sell_name: item.sell_name,
            sell: item.sell,
            percent: item.percent,
            tokenid: item.tokenid,
            buyurl: item.buyurl,
            sellurl: item.sellurl,
            buy_ask: item.buy_ask,
            sell_bid: item.sell_bid,
            sell_symbol: item.sell_symbol
          };
          this.TableData.push(elem);
        }
      });
      this.TableData.sort((a, b) => {
        if (a.percent < b.percent) {
          return 1;
        }
        if (a.percent > b.percent) {
          return -1;
        }
        // a должно быть равным b
        return 0;
      });
      this.dataSource = new MatTableDataSource(this.TableData);
      this.pushNotice(this.checkChange(this.TableData));
      setTimeout(() => {
        this.getHotbitProfits();
      }, this.timer);
    }, error => {
      console.log(error);
      this.getHotbitProfits();
    });
  }

  checkChange = (newData) => {
    const changes = [];
    if (this.tempTableData.length < 1) {
      this.tempTableData = newData;
      this.tempTableData.forEach((el) => {
        if (el.percent > this.redKoef) {
          changes.push(el);
        }
      });
    } else {
      this.TableData.forEach((el) => {
        if (el.percent > this.redKoef && this.checkElem(el, this.tempTableData) !== true) {
          changes.push(el);
        }
      });
      this.tempTableData = newData;
    }
    return changes;
  }

  checkElem = (elem: object, array: any[]) => {
    const isBelowThreshold = (currentValue) => JSON.stringify(currentValue) === JSON.stringify(elem);
    return array.some(isBelowThreshold);
  }

  pushNotice = (notice: any) => {
    this.notices = notice;
  }

  applyFilter = (event: Event) => {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  toUrl = (url: any) => {
    const a = document.createElement('a');
    a.setAttribute('href', url);
    a.dispatchEvent(new MouseEvent('click', {ctrlKey: true}));
  }

  hidePair = (pair: string, buy: string, sell: string, percent: number) => {
    this.hideDirection.push({pair, buy, sell, percent});
    console.log(this.hideDirection);
  }
}
