import { Injectable } from '@angular/core';
import {HttpClient, HttpErrorResponse, HttpHeaders} from '@angular/common/http';
import {Observable, throwError} from 'rxjs';
import {catchError} from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class ApiServiceService {

  // apiUrl = 'http://dev.xc.vp4.ru/';
  apiUrl = 'http://xc.vp4.ru/';
  // apiUrl = 'http://127.0.0.1:8000/';
  headers = new HttpHeaders();
  headersPub = new HttpHeaders();
  token = 'fZZ5mwam.UkBnR1iAHhK81HcZFxufRKhQrWDB1inL';

  constructor(private http: HttpClient) {
    this.setHeaders();
  }

  private setHeaders = () => {
    this.headersPub = this.headers.set('Content-Type', 'application/json');
    this.headers = this.headers.set('Content-Type', 'application/json');
    if (this.token) {
      this.headers = this.headers.set('X-Api-Key', this.token);
    }
  }

  getPublicApi(url): Observable<any> {
    return this.http.get(url, {headers: this.headersPub}).pipe(
      catchError(this.error)
    );
  }

  getApi(url): Observable<any> {
    return this.http.get(this.apiUrl + url, {headers: this.headers}).pipe(
      catchError(this.error)
    );
  }

  postApi(url, body): Observable<any> {
    return this.http.post<any>(this.apiUrl + url, body, {headers: this.headers}).pipe(
      catchError(this.error)
    );
  }

  // Handle Errors
  error = (error: HttpErrorResponse) => {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      errorMessage = error.error.message;
    } else {
      errorMessage = `${error.error.error}`;
    }
    return throwError(errorMessage);
  }
}
