import {Component, Input, OnChanges, SimpleChanges} from '@angular/core';
import {ApiServiceService} from '../../services/api-service.service';
import {Observable, of} from 'rxjs';

let tokenPrice: any[] = [];
const socketMsgs: any[] = [];
const socketStatuses: any[] = [];


@Component({
  selector: 'app-socket',
  templateUrl: './socket.component.html',
  styleUrls: ['./socket.component.css']
})
export class SocketComponent implements OnChanges {

  @Input() API_KEYS: any[] = [];
  @Input() tokenPrices: any[] = [];
  socketMsg = socketMsgs;
  socketStatus = socketStatuses;
  isShowing: boolean;
  wssIsAlive: boolean;
  lastEvent: any;

  constructor(private api: ApiServiceService) {
    // console.log(await publicClient.getServerTime());
    this.isShowing = false;
    this.api.getPublicApi('https://api.idex.io/v1/markets').subscribe((result: any[]) => {
      const tokens = [];
      result.forEach((res) => {
        tokens.push(res.market);
      });
      this.connect(tokens);
      tokenPrice = this.tokenPrices;
    });
  }

  // tslint:disable-next-line:typedef
  ngOnChanges(changes: SimpleChanges) {
    // tslint:disable-next-line:forin
    for (const propName in changes) {
      if (propName === 'tokenPrices') {
        tokenPrice = changes[propName].currentValue;
        if (tokenPrice.length > 0 && !this.isShowing) {
          this.showLog();
          this.isShowing = true;
        }
      }
    }
  }

  private connect(tokens): void {
    const wss = new WebSocket('wss://websocket.idex.io/v1');
    wss.onopen = (event) => {
      this.wssIsAlive = true;
      this.subscribe(wss, tokens);
    };
    wss.onmessage = (event) => {
      const sdata = JSON.parse(event.data);
      if (sdata.subscriptions) {
        // console.log(sdata.subscriptions);
      } else {
        console.log(sdata.type, sdata);
        setTimeout(() => {
          this.getComparToket(sdata.data).subscribe(res => {
            this.socketMsg.unshift(res);
          });
        }, 10);
        // const logData = JSON.stringify(sdata);
        // this.api.postApi('websocket_log/', {log: logData}).subscribe((res: any[]) => {
        //   console.log(res);
        // });
      }
    };
    wss.onerror = (err) => {
      console.error('Socket encountered error: ', err, 'Closing socket');
      wss.close();
    };
    wss.onerror = (event) => {
      console.error('WebSocket error observed:', event);
    };
    wss.onclose = (e) => {
      console.log('Close', e);
      console.log('Try reconnect', e);
      setTimeout(() => {
        this.connect(tokens);
      }, 1000);
    };
    // setInterval(() => {
    //   wss.send(JSON.stringify({type: 'ping', data: {}}));
    // }, 1000);
  }

  private subscribe(wss, tokens): any {
    const message = {
      method: 'subscribe',
      markets: tokens,
      subscriptions: [
        // 'tickers',
        'trades',
      ]
    };
    wss.send(JSON.stringify(message));
  }
  getComparToket(trade): Observable<any> {
    return of(this.comparToken<any>(trade));
  }
  private comparToken<T>(trade): any {
    console.log(trade);
    const data: any[] = [];
    this.lastEvent = trade;
    tokenPrice.forEach(token => {
      if (token.exch_direction.toLowerCase() === trade.m.replace('-ETH', '').toLowerCase()) {
        const tradePrice: number = parseFloat(trade.p);
        if (token.site === 'kyber') {
          data.push({
            time: new Date(trade.t).toLocaleString(),
            type: trade.s,
            market: 'Kyber',
            token: trade.m.replace('-ETH', ''),
            iprice: tradePrice,
            price: token.sell,
            percent: Number((token.sell - tradePrice) / (tradePrice / 100)).toFixed(2),
            urlbuy: 'https://exchange.idex.io/trading/' + trade.m.replace('-ETH', '') + '-ETH',
            urlsell: 'https://kyberswap.com/swap/ent-' + trade.m.replace('-ETH', '').toLowerCase()
          });
        }
        if (token.site === 'bancor') {
          data.push({
            time: new Date(trade.t).toLocaleString(),
            type: trade.s,
            market: 'Bankor',
            token: trade.m.replace('-ETH', ''),
            iprice: tradePrice,
            price: token.sell,
            percent: Number((token.sell - tradePrice) / (tradePrice / 100)).toFixed(2),
            urlbuy: 'https://exchange.idex.io/trading/' + trade.m.replace('-ETH', '') + '-ETH',
            urlsell: 'https://www.bancor.network/?q=' + trade.m.replace('-ETH', '').toLowerCase()
          });
        }
        // if (token.uniswaponebid) {
        //   data.push({
        //     time: new Date(trade.t).toLocaleString(),
        //     type: trade.s,
        //     market: 'Uniswap_V1',
        //     token: trade.m.replace('-ETH', ''),
        //     iprice: tradePrice,
        //     price: token.sell,
        //     percent: Number((token.sell - tradePrice) / (tradePrice / 100)).toFixed(2),
        //     urlbuy: 'https://exchange.idex.io/trading/' + trade.m.replace('-ETH', '') + '-ETH',
        //     urlsell: 'https://app.uniswap.org/#/swap?outputCurrency=' + token.uniswaponeid + '&use=v1'
        //   });
        // }
        if (token.site === 'uniswap') {
          data.push({
            time: new Date(trade.t).toLocaleString(),
            type: trade.s,
            market: 'Uniswap_V2',
            token: trade.m.replace('-ETH', ''),
            iprice: tradePrice,
            price: token.sell,
            percent: Number((token.sell - tradePrice) / (tradePrice / 100)).toFixed(2),
            urlbuy: 'https://exchange.idex.io/trading/' + trade.m.replace('-ETH', '') + '-ETH',
            urlsell: 'https://app.uniswap.org/#/swap?outputCurrency=' + token.contract
          });
        }
        if (trade.s === 'buy') {
          this.playAlarm();
        }
      }
    });
    return data;
  }

  private showLog(): void {
    this.api.getApi('websocket_log/').subscribe((res: any[]) => {
      const jDatas = [];
      res.forEach(elem => {
        if (!JSON.parse(elem.log).data) {
          jDatas.push(JSON.parse(elem.log));
        }
      });
      jDatas.forEach(el => {
        this.getComparToket(el).subscribe(result => {
          this.socketMsg.push(result);
        });
      });
    });
  }

  private playAlarm = () => {
    // const audio = new Audio();
    // audio.src = '../../../assets/beep-4.mp3';
    // audio.load();
    // audio.volume = 0.2;
    // audio.play();
  }
}
