import {Injectable} from '@angular/core';
import {HttpClient, HttpErrorResponse, HttpHeaders} from '@angular/common/http';
import {Observable, throwError} from 'rxjs';
import {catchError} from 'rxjs/operators';
import {ApiServiceService} from './api-service.service';

@Injectable({
  providedIn: 'root'
})
export class HotbitService {
  apiUrl = 'http://127.0.0.1:8000/hotbitv/';
  headers = new HttpHeaders();
  headersPub = new HttpHeaders();
  token = 'fZZ5mwam.UkBnR1iAHhK81HcZFxufRKhQrWDB1inL';
  IdexTokens: any[];
  HotbitTokens: any[];
  profits: any[];

  constructor(private http: HttpClient, private api: ApiServiceService) {
    this.setHeaders();
  }

  private setHeaders = () => {
    this.headersPub = this.headers.set('Content-Type', 'application/json');
    this.headersPub = this.headers.set('Access-Control-Allow-Origin', '*');
    this.headersPub = this.headers.set('Access-Control-Allow-Methods', 'GET, POST, OPTIONS, PUT, PATCH, DELETE');
    this.headers = this.headers.set('Content-Type', 'application/json');
    if (this.token) {
      this.headers = this.headers.set('X-Api-Key', this.token);
    }
  }

  getProfits = () => {
    this.api.postApi('hotbitv/', {}).subscribe(res => {console.log(res); });
  }

  // Handle Errors
  error = (error: HttpErrorResponse) => {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      errorMessage = error.error.message;
    } else {
      errorMessage = `${error.error.error}`;
    }
    return throwError(errorMessage);
  };
}
